import {ActivityType} from "../api.js";
import {eventBtn} from "./event-btn.js";
import {CREATE_VOTE, DELETE_VOTE} from "../events.js";
const voteSum = (vs, op, type) => vs.filter((x) => x.opinion_github_handle === op.github_handle && x.type === type).length;
const upvoteSum = (vs, op) => voteSum(vs, op, ActivityType.UPVOTE);
const downvoteSum = (vs, op) => voteSum(vs, op, ActivityType.DOWNVOTE);
const myVote = (user, vs, op, type) => vs.filter((x) => x.opinion_github_handle === op.github_handle && x.type === type && x.github_handle === user.login);
