    eventBtn,
    [
      CREATE_VOTE,
      {
        id,
        data: {
          entry_id: id,
          opinion_github_handle: opinion.github_handle,
          github_handle: user.login,
          type
        }
      }
    ],
    {
      class: "block transition ease-in-out duration-700 focus:outline-none disabled:opacity-50",
      disabled: voteLock
    },
    ["div", {...attribs}, content]
