import {StatusType, ActivityType} from "../api.js";
import {
  GET_ENTRY_W_ACTIVITY,
  SET_STATUS,
  ROUTE_TO_NEW_ENTRY
} from "../events.js";
import {opinionCard} from "./opinion-card.js";
import {opinionInput} from "./opinion-input.js";
import {metadata} from "./metadata.js";
import {status} from "./status.js";
import {eventBtn} from "./event-btn.js";
import {entryHeader} from "./entry-header.js";
import {transduce, comp, map, add, filter} from "../../_snowpack/pkg/@thi.ng/transducers.js";
const getVoteSum = (ogh, votes) => transduce(comp(filter((x) => x.opinion_github_handle === ogh), map((x) => x.type === ActivityType.UPVOTE ? 1 : -1)), add(0), votes);
export function entryDetail(ctx) {
  const views = ctx.views;
  const bus = ctx.bus;
  const id = decodeURI(ctx.views.route.deref().params.id);
  bus.dispatch(views.entries.deref()[id] ? [SET_STATUS, [StatusType.SUCCESS, "loaded from cache", true]] : [GET_ENTRY_W_ACTIVITY, id]);
  return () => {
    const id2 = decodeURI(views.route.deref().params.id);
    const entry = views.entries.deref()[id2];
    const votes = views.votes.deref()[id2];
    const sortByVotes = (a, b) => getVoteSum(b.github_handle, votes) - getVoteSum(a.github_handle, votes);
    const user = views.user.deref();
    return [
      "div",
      status,
      entry && entry.id && votes ? [
        "div",
        {class: "flex flex-col"},
        [entryHeader, entry],
        [metadata, entry],
        entry.opinions.sort(sortByVotes).map((x) => [opinionCard, x]),
        user.login && entry.opinions.map((x) => x.github_handle.toLowerCase()).indexOf(user.login.toLowerCase()) < 0 ? opinionInput : []
      ] : [
        "div",
        [
          "div",
          {class: "text-4xl m-auto p-6"},
          "no available data..."
        ],
        [
          "div",
          {class: "flex flex-row items-top text-gray-500"},
          [
            eventBtn,
            [ROUTE_TO_NEW_ENTRY, id2],
            {
              class: "block m-4 w-full"
            },
            [
              "div",
              {
                class: "shadow bg-purple-500 hover:bg-purple-400 focus:shadow-outline focus:outline-none text-white font-bold py-2 px-4 rounded"
              },
              "CREATE"
            ]
          ]
        ]
      ]
    ];
  };
}
