import {status} from "./status.js";
import {
  ROUTE_TO_ENTRY,
  SET_TEMP_ENTRY_TEMPLATE,
  SET_TEMP_ENTRY,
  UPDATE_ENTRY,
  GET_WIKI_TEMP
} from "../events.js";
import {entryInputForm} from "./entry-input-form.js";
export function editEntry(ctx) {
  const views = ctx.views;
  const bus = ctx.bus;
  const id = decodeURI(ctx.views.route.deref().params.id);
  const data = views.entries.deref()[id];
  if (!data) {
    return bus.dispatch([ROUTE_TO_ENTRY, id]);
  }
  bus.dispatch([SET_TEMP_ENTRY_TEMPLATE, {data}]);
  return () => {
    const user = views.user.deref();
    return [
      "div",
      status,
      user.login ? [
        entryInputForm,
        "tempEntry",
        SET_TEMP_ENTRY,
        GET_WIKI_TEMP,
        UPDATE_ENTRY
      ] : [
        "div",
        {class: "flex flex-col p-6 md:p-12"},
        [
          "p",
          ctx.ui.newsletterForm.title,
          "Please sign in before editing entry..."
        ]
      ]
    ];
  };
}
