import {TOGGLE_DEBUG} from "../events.js";
import {eventBtn} from "./event-btn.js";
export function debugContainer(ctx, debug, json) {
  const ui = ctx.ui.debug;
  return [
    "div#debug",
    ui.container,
    [eventBtn, [TOGGLE_DEBUG], ui.debugToggle, debug ? "close" : "open"],
    [
      "pre",
      debug ? ui.open : ui.close,
      [
        "div",
        {class: "flex"},
        ["div", {class: "flex-1 bg-gray-200"}, json]
      ]
    ]
  ];
}
