import {SET_OPINION} from "../events.js";
function row(_ctx, title, description, attribs, tag) {
  return [
    "div",
    {class: "block font-medium text-gray-800 text-lg mb-2"},
    [
      "div",
      {class: "flex flex-col"},
      title,
      description ? ["div", {class: "text-xs text-gray-500"}, description] : []
    ],
    [
      tag,
      {
        ...attribs,
        class: "shadow-inner appearance-none border rounded w-full py-2 px-3 bg-gray-200 text-gray-700 leading-tight focus:outline-none focus:shadow-outline focus:bg-white"
      }
    ]
  ];
}
function inputRow(ctx, title, description, attribs) {
  return row(ctx, title, description, attribs, "input");
}
function textareaRow(ctx, title, description, attribs) {
  return row(ctx, title, description, attribs, "textarea");
}
export function opinionInputRow(ctx, title, description, key, id, attribs) {
  const bus = ctx.bus;
  const cb = (e) => bus.dispatch([
    SET_OPINION,
    {
      id,
      key,
      value: e.target.value
    }
  ]);
  return inputRow(ctx, title, description, {...attribs, oninput: cb});
}
export function opinionTextareaRow(ctx, title, description, key, id, attribs) {
  const bus = ctx.bus;
  const cb = (e) => bus.dispatch([
    SET_OPINION,
    {
      id,
      key,
      value: e.target.value
    }
  ]);
  return textareaRow(ctx, title, description, {...attribs, oninput: cb});
}
export function entryInputRow(ctx, title, description, key, setEvent, attribs) {
  const bus = ctx.bus;
  const cb = (e) => {
    bus.dispatch([
      setEvent,
      {key, value: e.target.value}
    ]);
    key === "name" ? bus.dispatch([
      setEvent,
      {
        key: "id",
        value: e.target.value.toLowerCase()
      }
    ]) : null;
  };
  return inputRow(ctx, title, description, {...attribs, oninput: cb});
}
