import {status} from "./status.js";
import {
  SET_NEW_ENTRY_TEMPLATE,
  SET_NEW_ENTRY,
  CREATE_ENTRY,
  GET_WIKI_NEW
} from "../events.js";
import {entryInputForm} from "./entry-input-form.js";
export function newEntry(ctx) {
  const views = ctx.views;
  const bus = ctx.bus;
  const id = decodeURI(views.route.deref().params.id);
  const newEntry2 = views.newEntry.deref();
  const data = id === "null" || id === "" ? newEntry2 : {...newEntry2, ...{id, name: id}};
  bus.dispatch([SET_NEW_ENTRY_TEMPLATE, {data}]);
  return () => {
    const user = views.user.deref();
    return [
      "div",
      status,
      user.login ? [
        entryInputForm,
        "newEntry",
        SET_NEW_ENTRY,
        GET_WIKI_NEW,
        CREATE_ENTRY
      ] : [
        "div",
        {class: "flex flex-col p-6 md:p-12"},
        [
          "p",
          ctx.ui.newsletterForm.title,
          "Please sign in before adding new entry..."
        ]
      ]
    ];
  };
}
