export function newsletterForm(ctx) {
  const ui = ctx.ui.newsletterForm;
  return [
    "div",
    ui.root,
    ["h1", ui.title, " Newsletter "],
    [
      "form",
      {
        ...ui.form,
        action: "https://tinyletter.com/nopro-studio",
        method: "post",
        target: "popupwindow"
      },
      [
        "div",
        ui.container,
        [
          "input",
          {
            ...ui.input,
            type: "email",
            placeholder: "email address",
            name: "email",
            id: "tlemail"
          }
        ],
        ["input", {...ui.button, type: "submit", value: "SUBSCRIBE"}]
      ]
    ]
  ];
}
